import React, { useState } from "react";
import {
  numbers,
  upperCaseLetters,
  lowerCaseLetters,
  specialCharacters,
} from "../components/Character";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { COPY_Fail, COPY_SUCCESS } from "../components/message";
import { CopyToClipboard } from "react-copy-to-clipboard";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Checkbox from "@mui/material/Checkbox";
import Button from "@mui/material/Button";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import KeyIcon from "@mui/icons-material/Key";
import Input from "@mui/material/Input";

const Generator = () => {
  const [isCopied, setIsCopied] = useState(false);

  const onCopyText = () => {
    setIsCopied(true);
    setTimeout(() => {
      setIsCopied(false);
    }, 9000);
  };

  const [password, setPassword] = useState("");
  const [passwordLength, setPasswordLength] = useState(25);
  const [includeUpperCase, setIncludeUpperCase] = useState(false);
  const [includeLowerCase, setIncludeLowerCase] = useState(false);
  const [includeNumbers, setIncludeNumbers] = useState(false);
  const [includeSymbols, setIncludeSymbols] = useState(false);
  const handleGeneratePassword = () => {
    if (
      (passwordLength > 25 || passwordLength < 8) ||
      (!includeUpperCase &&
        !includeLowerCase &&
        !includeNumbers &&
        !includeSymbols)
    ) {
      notify(
        "You must select at least one of the checkbox and password must be between or equal to 8 and  25 characters!",
        true
      );
    } else {
      let characterList = "";
      if (includeNumbers) {
        characterList = characterList + numbers;
      }
      if (includeUpperCase) {
        characterList = characterList + upperCaseLetters;
      }
      if (includeLowerCase) {
        characterList = characterList + lowerCaseLetters;
      }
      if (includeSymbols) {
        characterList = characterList + specialCharacters;
      }
      setPassword(createPassword(characterList));
      notify("Password is generated successfully!", false);
    }
  };

  const createPassword = (characterList) => {
    let password = "";
    const characterListLength = characterList.length;

    for (let i = 0; i < passwordLength; i++) {
      const characterIndex = Math.round(Math.random() * characterListLength);
      password = password + characterList.charAt(characterIndex);
    }
    return password;
  };

  const copyToClipboard = (password) => {
    if (window.isSecureContext && navigator.clipboard) {
      navigator.clipboard.writeText(password);
    }
  };

  const notify = (message, hasError = false) => {
    if (hasError) {
      toast.error(message, {
        position: "top-center",
        autoClose: 4000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      toast(message, {
        position: "top-center",
        autoClose: 4000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const handleCopyPassword = (e) => {
    if (password === "") {
      notify(COPY_Fail, true);
    } else {
      copyToClipboard(password);
      notify(COPY_SUCCESS, false);
    }
  };

  return (
    <Container>
      <Grid container spacing={2}>
        <Grid item lg={4} md={2} sm={1} xs={12}></Grid>
        <Grid item lg={4} md={8} sm={10} xs={12}>
          <div className="generator">
            <h3 className="generator__header">
              <KeyIcon />
              &nbsp;Password Generator
            </h3>
            <div className="generator__password">
              <h5>{password}</h5>
              <CopyToClipboard text={password} onCopy={onCopyText}>
                <div className="copy-area">
                  <Button className="copy__btn">
                    <ContentCopyIcon
                      onClick={handleCopyPassword}
                      className={` copy-feedback ${
                        isCopied ? "onClick={handleCopyPassword}" : ""
                      }`}
                    ></ContentCopyIcon>
                  </Button>
                </div>
              </CopyToClipboard>
            </div>

            <div className="form-group">
              <label htmlFor="password-strength">Password length</label>
              <Input
                className="pw"
                defaultValue={passwordLength}
                onChange={(e) => setPasswordLength(e.target.value)}
                type="number"
                id="password-strength"
                name="password-strength"
                inputProps={{ min: 8, max: 25 }}
                min={8}
                max={25}
              />
            </div>
            <div className="form-group">
              <label htmlFor="uppercase-letters">
                Include Uppercase Letters
              </label>
              <Checkbox
                checked={includeUpperCase}
                onChange={(e) => setIncludeUpperCase(e.target.checked)}
                type="checkbox"
                id="uppercase-letters"
                name="uppercase-letters"
              />
            </div>
            <div className="form-group">
              <label htmlFor="lowercase-letters">
                Include Lowercase Letters
              </label>
              <Checkbox
                checked={includeLowerCase}
                onChange={(e) => setIncludeLowerCase(e.target.checked)}
                type="checkbox"
                id="lowercase-letters"
                name="lowercase-letters"
              />
            </div>
            <div className="form-group">
              <label htmlFor="include-numbers">Include Numbers</label>
              <Checkbox
                checked={includeNumbers}
                onChange={(e) => setIncludeNumbers(e.target.checked)}
                type="checkbox"
                id="include-numbers"
                name="include-numbers"
              />
            </div>
            <div className="form-group">
              <label htmlFor="include-symbols">Include Symbols</label>
              <Checkbox
                checked={includeSymbols}
                onChange={(e) => setIncludeSymbols(e.target.checked)}
                type="checkbox"
                id="include-symbols"
                name="include-symbols"
              />
            </div>

            <div className="padding">
              <Button
                onClick={handleGeneratePassword}
                className="generator__btn"
              >
                Generate
              </Button>

              <Button
                onClick={(e) => setPassword(e.target.value)}
                className="generator__btn"
              >
                Reset
              </Button>
            </div>
            <ToastContainer
              position="top-center"
              autoClose={5000}
              hideProgressBar={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              pauseOnHover
            />
          </div>
        </Grid>
        <Grid item lg={4} md={2} sm={1} xs={12}></Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item lg={12} md={12} sm={12} xs={12} >
          <div className="footer">
            <small>&copy;2022. All Rights Reserved. <br></br>
            Developed and Designed by Omar Barghashoon.</small>
          </div>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Generator;
